html, body, div, button, p, a, img, ul, li, form,
label, table, tbody, thead, tr, th, td {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

* {
    box-sizing: border-box;
}

.global {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding-top: 10px;
    width: 900px;
    font: normal 14px Arial, sans-serif;
}

label {
    padding-right: 5px;
}

@media only screen and (max-width: 900px) {
    .global {
        width: 100%;
    }

    .portfolios-container {
        display: flex;
        flex-flow: column;
        justify-content: center;
    }
}

.portfolio-form {
    margin: 15px auto;
    max-width: 310px;
}

.portfolios-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}

.portfolio-element {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 430px;
    margin: 10px;
    border: 1px solid black;
    border-radius: 5px;
}

.portfolio-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.portfolio-name {
    font-weight: bold;
    font-size: 17px;
    margin: 15px;
}

.del-portfolio {
    margin: 5px;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    background: url(del-button.png) no-repeat;
}

table, td, th {
    border: 1px solid black;
    border-collapse: collapse;
    text-align: center;
}

